import React, { useState, useEffect } from "react";
import {
  faCheck,
  faCircle,
  faImage,
  faPencilAlt,
  faTrash
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Checkmark = ({ selected, size, marginSize }) => {
  return (
    <div
      style={
        selected
          ? { right: 4, top: 4, position: "absolute", zIndex: "1" }
          : { display: "none" }
      }
    >
      <div title="View image" onClick={() => console.log("View Image")}>
        <FontAwesomeIcon
          icon={faCircle}
          color="white"
          style={{ position: "absolute", right: 0, fontSize: size }}
        />
        <FontAwesomeIcon
          icon={faCircle}
          color="#28a745"
          style={{
            position: "absolute",
            top: size / 16,
            right: size / 16,
            fontSize: (size * 7) / 8
          }}
        />
        <FontAwesomeIcon
          icon={faImage}
          color="white"
          style={{
            position: "absolute",
            top: size / 4,
            right: size / 4,
            fontSize: size / 2
          }}
        />
      </div>
      <div
        style={{ position: "absolute", right: size + marginSize }}
        title="Open Editor"
        onClick={() => console.log("Edit Image")}
      >
        <FontAwesomeIcon
          icon={faCircle}
          color="white"
          style={{ position: "absolute", right: 0, fontSize: size }}
        />
        <FontAwesomeIcon
          icon={faCircle}
          color="#ffc107"
          style={{
            position: "absolute",
            top: size / 16,
            right: size / 16,
            fontSize: (size * 7) / 8
          }}
        />
        <FontAwesomeIcon
          icon={faPencilAlt}
          color="white"
          style={{
            position: "absolute",
            top: size / 4,
            right: size / 4,
            fontSize: size / 2
          }}
        />
      </div>
      <div
        style={{ position: "absolute", right: (size + marginSize) * 2 }}
        title="Delete from selected"
        onClick={() => console.log("Remove Image")}
      >
        <FontAwesomeIcon
          icon={faCircle}
          color="white"
          style={{ position: "absolute", right: 0, fontSize: size }}
        />
        <FontAwesomeIcon
          icon={faCircle}
          color="#dc3545"
          style={{
            position: "absolute",
            top: size / 16,
            right: size / 16,
            fontSize: (size * 7) / 8
          }}
        />
        <FontAwesomeIcon
          icon={faTrash}
          color="white"
          style={{
            position: "absolute",
            top: size / 4,
            right: size / 4 + 1,
            fontSize: size / 2
          }}
        />
      </div>
    </div>
  );
};

const imgStyle = {
  transition: "transform .135s cubic-bezier(0.0,0.0,0.2,1),opacity linear .15s"
};
const selectedImgStyle = {
  borderRadius: 3,
  transform: "translateZ(0px) scale3d(0.9, 0.9, 1)",
  transition: "transform .135s cubic-bezier(0.0,0.0,0.2,1),opacity linear .15s"
};
const cont = {
  backgroundColor: "transparent",
  cursor: "pointer",
  overflow: "hidden",
  position: "relative"
};

const SelectedImage = ({ photo, margin, selected }) => {
  const [isSelected, setIsSelected] = useState(selected);
  //calculate x,y scale
  const sx = (100 - (30 / photo.width) * 100) / 100;
  const sy = (100 - (30 / photo.height) * 100) / 100;
  selectedImgStyle.transform = `translateZ(0px) scale3d(${sx}, ${sy}, 1)`;

  const handleOnClick = e => {
    setIsSelected(!isSelected);
  };

  useEffect(() => {
    setIsSelected(selected);
  }, [selected]);

  return (
    <div
      style={{
        margin,
        height: photo.height,
        width: photo.width,
        ...cont,
        display: "inline-flex"
      }}
      className={!isSelected ? "not-selected" : ""}
    >
      <Checkmark
        selected={isSelected ? true : false}
        size={30}
        marginSize={4}
      />
      <img
        alt={photo.title}
        style={
          isSelected ? { ...imgStyle, ...selectedImgStyle } : { ...imgStyle }
        }
        {...photo}
        onClick={handleOnClick}
      />
      <style>{`.not-selected:hover{outline:2px solid #06befa}`}</style>
    </div>
  );
};

export default SelectedImage;
